import { Observable, isObservable } from 'rxjs';
import { take } from 'rxjs/operators';

import { Command } from './command';
import { ExecuteResponse } from './execute-response';
import { AggregateId } from '../aggregate-id';

import { DomainEvent } from '../event/domain-event';
import { DomainEventPublisher } from '../event/domain-event.publisher';
import { DomainEventPayload } from '../event/domain-event.payload';

export abstract class CommandHandler {

	private readonly commandType: string;

	protected constructor(private readonly command: typeof Command | any, // any is required when inherited Command has more arguments than Command
						  private readonly domainEvent?: typeof DomainEvent,
						  protected readonly eventPublisher?: DomainEventPublisher) {
		this.commandType = this.createCommandInstance().getMessageType();
	}

	abstract handle(command: Command): ExecuteResponse | Observable<ExecuteResponse>;

	forCommand(command: Command): boolean {
		return this.commandType === command.getMessageType();
	}

	handleCommand(command: Command): void {
		const result = this.handle(command);

		if (isObservable(result)) {
			(result as Observable<ExecuteResponse>)
				.pipe(
					take(1)
				)
				.subscribe((res: void | DomainEventPayload) => {

					// trigger event
					this.dispatchEvent(command, res);
				});
		} else {
			this.dispatchEvent(command, result as ExecuteResponse);
		}
	}

	private dispatchEvent(command: Command, result?: ExecuteResponse): void {

		if (this.domainEvent && this.eventPublisher) {

			let aggregateId: AggregateId = command.aggregateId;

			const event = new (this.domainEvent as any)(aggregateId);

			event.setRequestCommand(command);

			if (result) {
				event.setPayload(result);
			}

			this.eventPublisher.publish(event);
		}
	}

	private createCommandInstance(): Command {
		const args: Array<void> = [],
			argumentLength = this.command.constructor.length;

		args.fill(undefined, 0, argumentLength);

		return (new (this.command as any)(...args));
	}
}
