import { Inject, Injectable } from '@angular/core';
import { Observable, Subject, Operator } from 'rxjs';
import { filter } from 'rxjs/operators';

import { Command } from './command';
import { FILTERED_COMMAND_STREAM } from './filtered-command-stream';
import { CommandHandler } from './command.handler';

@Injectable()
export class CommandBus<C = Command> extends Observable<C> {

	constructor(@Inject(FILTERED_COMMAND_STREAM) commandsStream?: Subject<Command>) {
		super();

		if (commandsStream) {
			this.source = commandsStream;
		}
	}

	lift<R>(operator: Operator<C, R>): Observable<R> {
		const observable = new CommandBus<R>();
		observable.source = this;
		observable.operator = operator;
		return observable;
	}

	ofCommand<C2 extends C>(...commandTypes: Array<string>): any {
		return (this as CommandBus<any>)
			.pipe(
				filter((command: Command) => {
					return commandTypes.some((commandType: string) => command.ofMessageType(commandType));
				})
			);
	}

	ofHandler<C2 extends C>(...handlers: Array<CommandHandler>): any {
		return (this as CommandBus<any>)
			.pipe(
				filter((command: Command) => {
					return handlers.some((handler: CommandHandler) => handler.forCommand(command));
				})
			);
	}

	ofNullHandler<C2 extends C>(handlers: Array<CommandHandler>): any {
		return (this as CommandBus<any>)
			.pipe(
				filter((command: Command) => {

					if (!handlers) {
						return true;
					}

					return !handlers.some((handler: CommandHandler) => handler.forCommand(command));
				})
			);
	}

}
