import { StaticProvider, Type } from '@angular/core';

import { CommandHandler } from './command/command.handler';
import { COMMAND_HANDLERS } from './command/command-handlers';
import { DOMAIN_EVENT_HANDLERS } from './event/domain-event-handlers';
import { DomainEventHandler } from './event/domain-event.handler';

export function provideCommandHandlers(handlers: Array<Type<CommandHandler>>): Array<StaticProvider> {
	return handlers.map((handler: Type<CommandHandler>) => {
		return {
			provide: COMMAND_HANDLERS,
			useClass: handler,
			multi: true
		};
	}) as any;
}

export function provideEventHandlers(handlers: Array<Type<DomainEventHandler>>): Array<StaticProvider> {
	return handlers.map((handler: Type<DomainEventHandler>) => {
		return {
			provide: DOMAIN_EVENT_HANDLERS,
			useClass: handler,
			multi: true
		};
	}) as any;
}
