import {
	AggregateId
} from '../../../domain/aggregate-id';
import {
	Aggregate
} from '../../../domain/command/aggregate';

import {
	ReadModel
} from '../../../domain/read/read-model';

import {
	ReadModelStore
} from '../../../domain/read/store/read-model.store';

import { InMemoryStore } from '../in-memory.store';

export abstract class InMemoryReadModelStore<T extends ReadModel, S extends Aggregate> extends ReadModelStore<T> {

	protected constructor(private readonly inMemoryStore: InMemoryStore<S>) {
		super();
	}

	abstract toReadModel(aggregate: S): T;

	getById(aggregateId: AggregateId): T {
		return this.getValue(aggregateId);
	}

	getAll(): Array<T> {
		return this.inMemoryStore.getAll()
				   .map((aggregate: S) => this.toReadModel(aggregate));
	}

	private getValue(aggregateId: AggregateId): T {
		const aggregate = this.inMemoryStore.get(aggregateId);

		if (aggregate) {
			return this.toReadModel(aggregate);
		} else {
			return null;
		}
	}

}
