import {
	AggregateId
} from '../../../domain/aggregate-id';

import {
	Aggregate,
} from '../../../domain/command/aggregate';
import {
	AggregateStore,
} from '../../../domain/command/store/aggregate.store';
import {
	AggregateStoreRegister
} from '../../../domain/command/store/aggregate-store.register';

import { PersistAnemia } from '../persist-anemia';
import { PersistStateStore } from '../persist-state.store';


export abstract class PersistAggregateStore<T extends Aggregate, S extends PersistAnemia> extends AggregateStore<T> {

	protected constructor(private readonly stateStore: PersistStateStore<S>,
						  private readonly aggregateStoreRegister: AggregateStoreRegister) {
		super();
		this.aggregateStoreRegister.register(this);
	}

	abstract toAnemia(aggregate: T): S;

	abstract fromAnemia(anemia: S): T;

	save(aggregate: T): void {
		this.saveValue(aggregate);
	}

	getById(aggregateId: AggregateId): T {
		return this.getValue(aggregateId);
	}

	private saveValue(aggregate: T): void {

		const anemia = this.toAnemia(aggregate);

		this.stateStore.set(anemia);
	}

	private getValue(aggregateId: AggregateId): T {
		const anemia = this.stateStore.get(aggregateId);

		if (anemia) {
			return this.fromAnemia(anemia);
		} else {
			return null;
		}
	}

}
