import {
	AggregateId
} from '../../../domain/aggregate-id';

import {
	Aggregate
} from '../../../domain/command/aggregate';
import {
	AggregateStore
} from '../../../domain/command/store/aggregate.store';
import {
	AggregateStoreRegister
} from '../../../domain/command/store/aggregate-store.register';

import { InMemoryStore } from '../in-memory.store';

export abstract class InMemoryAggregateStore<T extends Aggregate> extends AggregateStore<T> {

	protected constructor(private readonly inMemoryStore: InMemoryStore<T>,
						  private readonly aggregateStoreRegister: AggregateStoreRegister) {
		super();
		this.aggregateStoreRegister.register(this);
	}

	save(aggregate: T): void;
	save(aggregates: Array<T>): void;
	save(arg: T | Array<T>): void {

		if (Array.isArray(arg)) {
			arg.forEach((aggregate: T) => {
				this.inMemoryStore.set(aggregate);
			});
		} else {
			const aggregate = arg;

			this.inMemoryStore.set(aggregate);
		}
	}

	getById(aggregateId: AggregateId): T {
		return this.inMemoryStore.get(aggregateId);
	}

	getAll(): Array<T> {
		return this.inMemoryStore.getAll();
	}

	remove(aggregateId: AggregateId): void {
		this.inMemoryStore.delete(aggregateId);
	}

}
