import {
	AggregateId
} from '../../../domain/aggregate-id';
import {
	ReadModel
} from '../../../domain/read/read-model';
import {
	ReadModelStore
} from '../../../domain/read/store/read-model.store';

import { PersistAnemia } from '../persist-anemia';
import { PersistStateStore } from '../persist-state.store';

export abstract class PersistReadModelStore<T extends ReadModel, S extends PersistAnemia> extends ReadModelStore<T> {

	protected constructor(private readonly stateStore: PersistStateStore<S>) {
		super();
	}

	abstract fromAnemia(anemia: S): T;

	getById(aggregateId: AggregateId): T {
		return this.getValue(aggregateId);
	}

	getAll(): Array<T> {
		return this.getAllValues();
	}

	private getAllValues(): Array<T> {
		const anemias = this.stateStore.getAll();

		return anemias.map((anemia) => this.fromAnemia(anemia));
	}

	private getValue(aggregateId: AggregateId): T {
		const anemia = this.stateStore.get(aggregateId);

		if (anemia) {
			return this.fromAnemia(anemia);
		} else {
			return null;
		}
	}

}
